@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap");

:root {
  --maxWidth-phone-SAFEZONE: 990px;
  --white: #ffffff;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Outfit", sans-serif;
  user-select: none; /* Prevent text selection */
  /* outline: #fff 1px dashed; */
}

body {
  background: #000;
}

h1,
h2,
h3,
h4,
h5,
a,
p {
  color: #fff;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

label {
  color: white;
}


input[type="radio"] {
  height: 30px;
  aspect-ratio: 1 / 1;
}

.btn {
  padding: 8px 22px;
  font-size: 1rem;
  text-transform: uppercase;
  background: #1e0a34;
  color: #f1e8ff;
  border: 1.7px solid #fff;
  font-weight: 600;
  cursor: pointer;
}

.btn.icon {
  padding: 16px;
  border-radius: 50%;
}

.btn.enter {
  background: #06180b;
  color: #edffed;
}

.btn.disabled {
  opacity: 0.47;
  cursor: not-allowed;
}

button.btn:disabled {
  opacity: 0.47;
  cursor: not-allowed;
}

button.btn:disabled:hover {
  background: #ff00fc;
  color: black;
  transition: 0s;
}

.btn.disabled:hover {
  background: #ff00fc;
  color: black;
  transition: 0s;
}

.btn.btn-light.disabled:hover {
  background: #191719;
  color: white;
  transition: 0s;
}

.btn-light {
  background: transparent;
  color: #fff;
}

.btn-alt {
  background: #00ffff;
}

.btn:hover {
  background: #191719;
  color: #fff;
  transition: 0.3s;
}

.email-form {
  display: flex;
  flex-direction: column;
  width: 250px;
  align-items: center;
}

.email-form input {
  background-color: hsl(263, 47%, 15%);
  border-style: solid;
  border-width: 1.6px;
  border-color: hsl(264, 86%, 92%);
  border-radius: 1.4px;
  padding: 8px 12px;
  width: 240px;
  margin-bottom: 9px;
  color: white;
}

.email-form input::placeholder {
  color: rgb(217, 190, 245);
}

input.search-s1 {
  font-size: 13px;
  width: 280px;
  padding: 6px 10px;
}

input.search-s2 {
  font-size: 15px;
  width: 280px;
  padding: 8px 12px;
}

.hidden {
  display: none;
}

.span-after,
.span-before {
  display: inline-block;
}

.inline-after,
.inline-before {
  display: inline;
}

.span-before::after,
.before-inline::after {
  content: "\00a0\00a0\00a0";
}

.span-after::before {
  content: "\00a0\00a0\00a0";
}

@media screen and (max-width: 1040px) {
}

.standard-page {
  width: 100%;
  height: 100%;
  background: hsl(264.2, 22.1%, 5.1%);
  padding: 5rem 0;
}

.standard-page p {
  line-height: 1.6;
}

.standard-page-container {
  margin: auto;
}

.standard-page-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 1.4rem;
}

.standard-page-content.inactive {
  display: none;
}

.standard-uncentered-content {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 4rem 5.6rem;
}

.standard-section {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 20px 0 50px 0;
}
.standard-centered-section {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0 50px 0;
}

.standard-divider {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 20px 0 30px 0;
}
.standard-centered-divider {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0 30px 0;
}

.standard-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: start;
  margin-bottom: 110px;
  margin-top: 30px;
}

.standard-list.inner {
  margin-bottom: 20px;
  margin-top: 4px;
}
.standard-list.inner li {
  padding: 4px 0;
}
.standard-list.inner li::before {
  text-indent: 30px;
}

.standard-list h1 {
  font-size: 16.2px;
  padding: 0 0;
}


.standard-list .hidden {
  display: inline;
  visibility: hidden;
}

.standard-list.dotted ul li::before, .standard-list.inner.dotted ul li::before {
  content: "\2022";
  display: inline-block;
  margin-left: -1rem;
  color: white;
}
.standard-list.dashed ul li::before, .standard-list.inner.dashed ul li::before {
  content: "-";
  display: inline-block;
  margin-left: -1rem;
  color: white;
}
.standard-list.numbered ol li::before, .standard-list.inner.numbered ol li::before {
  content: counter(li);
  display: inline-block;
  margin-left: -1rem;
  color: white;
  text-decoration: none;
  text-decoration-style: none;
  text-indent: none;
  text-transform: none;
}
.standard-list ol, .standard-list ul {
  list-style: none;
  
}

.standard-list.inner.none ul li {
  padding: 6px 0;
}

.standard-list.inner.none ul li::before {
  content: none;
  text-indent: 0;
}

.standard-list.numbered ol li, .standard-list.inner.numbered ol li {
  counter-increment: li;
}

.standard-list h1,
.standard-list.dotted h1,
.standard-list.numbered h1,
.standard-list.dashed h1 {
  display: inline-block;
  margin-left: 0.8rem;
}

.standard-header {
  font-size: 23px;
  padding: 24px 10%;
}

.standard-header.s {
  font-size: 23px;
}

.standard-header.m {
  font-size: 26px;
}

.standard-header.l {
  font-size: 32px;
}

.standard-title {
  font-size: 23px;
  padding: 44px 2%;
}

.standard-subtitle {
  font-size: 20px;
  padding: 24px 2%;
}

.standard-label {
  font-size: 17px;
  padding: 24px 2%;
}
.standard-label.l {
  font-size: 20px;
}
.standard-label.s {
  font-size: 15px;
}
.standard-label.xs {
  font-size: 13px;
}
.standard-label.xxs {
  font-size: 12px;
}
.standard-label.xxxs {
  font-size: 11px;
}

.standard-desc {
  font-size: 16px;
  padding: 20px 2%;
}

.standard-header-container {
  display: flex;
  width: 60%;
  margin: 80px 0px;
}

.standard-header-content {
  display: flex;
  width: 100%;
  background-color: hsl(300, 16%, 9%);
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 6% 13%;
}

.standard-header-content .standard-header.main {
  font-size: 25px;
  padding: 30px 11% 0px 11%;
  font-weight: 600;
}

.standard-header-content .standard-list ul li,
.standard-header-content .standard-list ol li {
  font-weight: 500;
}
.standard-header-content .standard-list ul li::before,
.standard-header-content .standard-list ol li::before {
  font-weight: 500;
}

.standard-analytics-grid {
  margin-top: 100px;
  width: 98%;
  background-color: rgb(78, 62, 77);
  display: grid;
  /*? Customize your own */
}
/*! Row based sample 
  grid-template-columns: [start] 100% [end];
  grid-template-rows: [start1] 50px [end1 start2] 75px [end2] repeat(
      12,
      60px [row-start]
    );
  grid-template-areas: "label-main";
*/
/*! Column based sample 
  grid-template-columns: repeat(4, 1fr [col-start]);
  grid-template-rows: [row1-start] 50px [row1-end row2-start] 65px [row2-end row3-start] 600px [row3-end];
  grid-template-areas:
    "label-main label-main label-main label-main";
*/

.standard-analytics-grid .analytic-row {
  width: 100%;
  display: flex;
}

.standard-analytics-grid .analytic-label {
  font-size: 20px;
}

.standard-analytics-grid .analytic-label-box {
  background-color: rgb(130, 83, 206);
  border: 1.2px solid white;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.standard-analytics-grid .analytic-label-box.second {
  background-color: rgb(68, 46, 103);
  width: 100%;
}

.standard-analytics-grid .analytic-label-box.first {
  grid-area: label-main;
  background-color: rgb(92, 66, 135);
  width: 100%;
}

.standard-analytics-grid .data-box {
  background-color: rgb(130, 83, 206);
  border: 1.2px solid white;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 10px 18px;
  text-align: center;
}

.standard-analytics-grid .data-box.no {
  background-color: rgb(255, 70, 70);
}
.standard-analytics-grid .data-box.yes {
  background-color: rgb(97, 255, 105);
}

.standard-analytics-grid .data-box:nth-child(1) {
  background-color: rgb(68, 46, 103);
  width: 100%;
}

.standard-analytics-grid .data-box .data {
  font-size: 13px;
}

.standard-analytics-grid .data-box .data.label {
  font-size: 14px;
}

.standard-analytics-grid .data-box .data.value {
  font-size: 13px;
}

.standard-analytics-grid .data-box .text-content {
  display: flex;
  align-items: center;
}

.standard-page.theme-opacity {
  background: rgba(0, 0, 0, 1);
  background: linear-gradient(
    90deg,
    hsla(260, 53%, 4%, 0.28) 0%,
    hsla(260, 53%, 4%, 0.612) 15%,
    hsla(260, 53%, 4%, 0.712) 30%,
    hsla(260, 53%, 4%, 0.712) 100%
  );
}

.standard-page.theme-opacity2 {
  background: rgba(0, 0, 0, 1);
  background: linear-gradient(
    90deg,
    hsla(260, 53%, 4%, 0.28) 0%,
    hsla(260, 53%, 4%, 0.172) 15%,
    hsla(260, 53%, 4%, 0.112) 30%,
    hsla(260, 53%, 4%, 0.112) 100%
  );
}

.standard-page.theme-opacity3 {
  background: rgba(0, 0, 0, 1);
  background: linear-gradient(
    90deg,
    hsla(260, 53%, 4%, 0.28) 0%,
    hsla(260, 53%, 4%, 0.07) 4%,
    hsla(260, 53%, 4%, 0) 8%,
    hsla(260, 53%, 4%, 0) 100%
  );
}

.reveal-content {
  /* display: none; */
}

.reveal-content .active {
  display: initial;
}


/**/
