.option-btn-container {
  width: 220px;
  height: 38px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(122, 40, 199);
}

.option-btn-container h1 {
  font-size: 17px;
}

ul.column-option-selection {
  display: flex;
  flex-direction: column;
}

ul.column-option-selection li {
  margin-bottom: 16px;
}

ul.column-option-selection li:last-child {
  margin-bottom: 0px;
}

ul.row-option-selection {
  display: flex;
}

ul.row-option-selection li {
  margin-left: 16px;
}

ul.row-option-selection li:last-child {
  margin-bottom: 0px;
}
