.body {
    width: 100%;
    background: hsl(264.2,22.1%,5.1%);
    padding: 5rem 0;
}

.body-container {
    margin: auto;
}

.body-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 1.4rem;
}

.body-content .title {
    padding: 34px 0;
}

.body .data-box {
    width: 98%;
    max-width: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .body .data-content,
  .body .data-content-empty-line {
    width: 100%;
    height: 38px;
    display: flex;
    align-items: center;
  }

  .body .data-content h3 {
    font-size: 14px;
  }
  
  .body .text-content {
    flex-grow: 1;
    display: flex;
    align-items: center;
  }
  
  .body .slider-content {
    width: 45%;
  }
  
  .body .data {
    font-size: 20px;
  }
  
  .body .data.label {
    padding-right: 10px;
  }
  
  .body .data.value {
    font-size: 21px;
  }
  .body .data.value.negative {
    color: hsl(343, 100%, 50%);
  }
  .body .data.value.positive {
    color: hsl(131, 100%, 50%);
  }
  .body .data.value.unchanged {
    color: hsl(0, 0%, 77%);
  }

  .body .comparison-data-box {
    
  }

  .body .comparison-data-box .split-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    flex-grow: 1;
    height: 100%;
    background: hsl(264, 36%, 5%);
    padding: 0px 20px;
    outline: 2px solid white;
  }

  .body .comparison-data-box .name {
    font-size: 24px;
  }

  .body .comparison-data-box .header-box {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

